import uniLoadMore from "@/components/uni-load-more/uni-load-more.vue"
export default {
	components:{
		uniLoadMore
	},
	data() {
		return {
			active:1,
			tabActive:0,
			sh:'',
			dataList:[],
			page:1,
			pageSize:7,
			total:0,
			total1:0,
			total2:0,
			total3:0,
			more:'',
			uid:'',
			token:''
		}
	},
	computed: {
	     noMore () {
	       return this.dataList.length >= this.total
	     },
	   },
	   mounted(){
	   	var that=this
		var userInfo=uni.getStorageSync('userInfo')
		if(userInfo!='' & userInfo!=null & userInfo!=undefined){
			this.uid=userInfo.uid
			this.token=userInfo.token
			this.getList(this.page)
		}
	   	setTimeout(function(){
	   		that.$getHeight('#sv',(res) =>{
	   			that.sh=res
	   		})
	   	},0)
	   },
	methods: {
		distributionOrderdetail(){
			uni.navigateTo({
				url:'/pages/distributionOrderdetail/distributionOrderdetail'
			})
		},
		toggle(t){
			this.active=t
			console.log(t)
			this.reset() 
			this.getList(this.page)
		},
		reset(){
			this.page=1
			this.total=0
			this.dataList=[]
			this.more=''
		},
		getList(p){
			var that=this
			var params={
				uid:this.uid,
				token:this.token,
				level:this.active,
			  page:p,
			  pagesize: this.pageSize
			}
			if(this.page==1){
				this.$loading()
			}
			  var url='&r=api.member.agent.team'
			  this.$apiPost(url,params).then((res) =>{
				  if(that.active==1){
					  that.total=res.total1
				  }else if(that.active==2){
					   that.total=res.total2
				  }else{
					   that.total=res.total3
				  }
				  that.total1=res.total1
				  that.total2=res.total2
				  that.total3=res.total3
				  that.dataList=that.dataList.concat(res.data)
				  that.more=''
				  if(that.page==1){
				  	uni.hideLoading()
				  }
			  })
		},
		toBottom(){
			if(this.noMore){
				this.more='noMore'
				return;
			}
			var that=this
			this.more='loading'
		  // setTimeout(function(){
			  that.page++
			  that.getList(that.page)
		  // },2000)
		},
	}
}
